import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { environment } from '../../../environments/environment';
import { User } from '../../model';

@Injectable({ providedIn: 'root' })
export class AuthService {
    private currentUserSubject: BehaviorSubject<User>;
    public currentUser: Observable<User>;

    constructor(private http: HttpClient) {
        this.currentUserSubject = new BehaviorSubject<User>(JSON.parse(localStorage.getItem('currentUser')));

        this.currentUser = this.currentUserSubject.asObservable();
    }

    public get currentUserValue(): User {
        return this.currentUserSubject.value;
    }

    login(userName: string, userPassword: string) {
       // return this.http.post<any>(`${environment.apiUrl}/user/auth/signin`, { username, password })
         return this.http.post<any>(`/api.scm360.solution/v1/user/auth/signin`, { userName, userPassword })
            .pipe(map(res => {
                // store user details and jwt token in local storage to keep user logged in between page refreshes
                let success = false;
                if (res.count === 1) {
                  localStorage.setItem('currentUser', JSON.stringify(res.data[0]));
                  this.currentUserSubject.next(res.data[0]);
                  success = true;
                } else {
                  localStorage.removeItem('currentUser');
                  this.currentUserSubject.next(null);
                  success = false;
                }

                return {success, user: res.data[0]};

            }));
    }

    logout() {
        // remove user from local storage to log user out
        localStorage.removeItem('currentUser');
        this.currentUserSubject.next(null);

    }


    isLogin(): boolean {
      if (localStorage.getItem('currentUser')) {
        return true;
      } else {
        return false;
      }
    }
    getUser(): User {

      return JSON.parse(localStorage.getItem('currentUser'));
    }
}
