import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiService } from 'src/app/service/api/api.service';
import { AuthService } from 'src/app/service/auth/auth.service';
import { DropdownService } from 'src/app/service/dropdown/dropdown.service';


@Component({
  selector: 'app-client-management',
  templateUrl: './client-management.component.html',
  styleUrls: ['./client-management.component.css']
})
export class ClientManagementComponent implements OnInit {
  name: string;
  id: number;
  logo: string;
  businessAdress: string;
  street: string;
  city: string;
  state: string;
  country: string;
  zipecode: string;
  telephone: string;
  stateList: string[];
  usStateURL = '/api.scm360.solution/v1/us_state';
  uploadImageURL = '/api.scm360.solution/v1/user/photo/add';
  submitClientURL = '/api.scm360.solution/v1/superadmin/client_management/add';
  imageError: string;
  isImageSaved: boolean;
  cardImageBase64: string;
  constructor(private fb: FormBuilder,
              private download: DropdownService,
              private http: HttpClient,
              private authService: AuthService,
              private router: Router,
              private route: ActivatedRoute ) {


              this.stateList = [];


        }

  ngOnInit(): void {
    const headers =  new HttpHeaders()
          .set('Content-Type', 'application/json; charset=utf-8')
          .set('scm360Token', this.authService.getUser().accessToken);

    this.http.get<any>(this.usStateURL, {headers})
              .subscribe((res) => {
                   res.data.map(state => this.stateList.push(state));
              });
  }

  onUploadCompanyImage(uploadedFiles: FileList) {



    const reader = new FileReader();

    const uploadedFile = uploadedFiles[0];



    reader.readAsDataURL(uploadedFile);

    reader.onload = (event) => {

      const headers: HttpHeaders = new HttpHeaders()
        .set('Content-Type', 'application/json; charset=utf-8')
        .set('scm360Token', this.authService.getUser().accessToken);
      const imgBase64Path = event.target.result;
      this.cardImageBase64 = imgBase64Path.toString();
      this.isImageSaved = true;

      const body = {
        imageData: reader.result
      };
      this.http.post<any>(this.uploadImageURL, JSON.stringify(body), {headers})
        .subscribe(({data}) => {

              this.logo = data[0].id;

        });

    };
}

openFile() {

  (document.querySelector('#companyImage') as HTMLInputElement).click();

}

removeImage() {
  this.cardImageBase64 = null;
  this.isImageSaved = false;
}


onSubmitClient() {



  const company = {
    name: this.name,
    logo: this.logo,
    street: this.street,
    businessAddress: this.businessAdress,
    city: this.city,
    state: this.state,
    country: this.country,
    zipCode: this.zipecode,
    telephone: this.telephone


  };
  console.log(company);
  const headers: HttpHeaders = new HttpHeaders()
        .set('Content-Type', 'application/json; charset=utf-8')
        .set('scm360Token', this.authService.getUser().accessToken);
  this.http.post<any>(this.submitClientURL, JSON.stringify(company), {headers})
        .subscribe(res => console.log(res));

}

}
