import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';


import { AuthGuard } from 'src/app/helper/auth.guard';
import { HomeComponent } from './home/home.component';
import { DashboardComponent } from 'src/app/components/dashboard/dashboard/dashboard.component';
import { InboundReceivingComponent } from 'src/app/components/inbound/inbound-receiving/inbound-receiving.component';
import { OutboundShippingComponent } from 'src/app/components/outbound/outbound-shipping/outbound-shipping.component';
import { OutboundConfirmationComponent } from 'src/app/components/outbound/outbound-confirmation/outbound-confirmation.component';
import { InspectionReportComponent } from 'src/app/components/inspection-report/inspection-report.component';
import { IncidentReportComponent } from 'src/app/components/incident-report/incident-report.component';
import { NoAccessAdvisoryComponent } from 'src/app/components/no-access-advisory/no-access-advisory.component';
import { ClientListComponent } from 'src/app/components/superadmin/client-list/client-list.component';
import { ClientDetailComponent } from 'src/app/components/superadmin/client-detail/client-detail.component';
import { ConfirmationComponent } from 'src/app/components/confirmation/confirmation.component';
import { ClientManagementComponent } from 'src/app/components/superadmin/client-management/client-management.component';


const routes: Routes = [
  {
   path: '',
   component: HomeComponent,
   canActivate: [AuthGuard],
   children: [
     {
     path: '',
     component: DashboardComponent
    },
    {
      path: 'inbound',
      component: InboundReceivingComponent
    },
    {
      path: 'outbound-shipping',
      component: OutboundShippingComponent
    },
    {
      path: 'outbound-shipping',
      component: OutboundConfirmationComponent
    },
    {
      path: 'inspection',
      component: InspectionReportComponent
    },
    {
      path: 'incident',
      component: IncidentReportComponent
    },
    {
      path: 'opps',
      component: NoAccessAdvisoryComponent

    },
    {
      path: 'client-list',
      component: ClientListComponent
    },
    {
      path: 'client-detail/:id',
      component: ClientDetailComponent

    },
    {
      path: 'submit-success',
      component: ConfirmationComponent
    },
    {
      path: 'submit_success_outbound',
      component: OutboundConfirmationComponent
    },
    { path: 'client-management',
     component: ClientManagementComponent
    },

  ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [
    RouterModule,
    FormsModule,
    ReactiveFormsModule
  ]
})
export class HomeRoutingModule { }
